* {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
	outline: none;
}
$hippie-blue: #4D96A9;
$malibu-blue: #8FE3F9;
$mantree-grey: #87879D;
$alto-grey: #D8D8D8;
$ebony-black: #28283D;
$alabaster-white: #FAFAFA;
$delunge-violet: #855FB1;
font-face {
	font-family: 'Red-Hat-Display';
	src: 
		local('fonts/red-hat-display'), url('/src/assets/fonts/red-hat-display/red-hat-display-light.ttf') format('ttf'),
		local('fonts/red-hat-display'), url('/src/assets/fonts/red-hat-display/red-hat-display-light.woff') format('woff'),
		local('fonts/red-hat-display'), url('/src/assets/fonts/red-hat-display/red-hat-display-light.woff2') format('woff2');
	font-display: block;
	font-weight: 300;
}

